import { NavLink } from "react-router-dom";
import { useContext, useState } from "react";

import { styled } from '@mui/material/styles';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import { ThemeContext } from "../../App";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { Dropdown } from '@mui/base/Dropdown';
import { Menu } from '@mui/base/Menu';
import { MenuButton } from '@mui/base/MenuButton';
import { MenuItem, menuItemClasses } from '@mui/base/MenuItem';

import "./navigation.scss";

const Navigation = () => {

  const theme = useContext(ThemeContext);
  const [checked, setChecked] = useState(false)
  const handleChange = (e) => {
    setChecked(e.target.checked ? true : false)
    theme.setTheme(e.target.checked ? 'dark' : 'light')
  }

  
    const createHandleMenuClick = (menuItem) => {
      return () => {
        console.log(`Clicked on ${menuItem}`);
      };
    }
  

  const MaterialUISwitch = styled(Switch)(({ theme }) => ({
    width: 62,
    height: 34,
    padding: 7,
    '& .MuiSwitch-switchBase': {
      margin: 1,
      padding: 0,
      transform: 'translateX(6px)',
      '&.Mui-checked': {
        color: '#fff',
        transform: 'translateX(22px)',
        '& .MuiSwitch-thumb:before': {
          backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
            '#fff',
          )}" d="M4.2 2.5l-.7 1.8-1.8.7 1.8.7.7 1.8.6-1.8L6.7 5l-1.9-.7-.6-1.8zm15 8.3a6.7 6.7 0 11-6.6-6.6 5.8 5.8 0 006.6 6.6z"/></svg>')`,
        },
        '& + .MuiSwitch-track': {
          opacity: 1,
          backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
        },
      },
    },
    '& .MuiSwitch-thumb': {
      backgroundColor: theme.palette.mode === 'dark' ? '#003892' : '#001e3c',
      width: 32,
      height: 32,
      '&:before': {
        content: "''",
        position: 'absolute',
        width: '100%',
        height: '100%',
        left: 0,
        top: 0,
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
          '#fff',
        )}" d="M9.305 1.667V3.75h1.389V1.667h-1.39zm-4.707 1.95l-.982.982L5.09 6.072l.982-.982-1.473-1.473zm10.802 0L13.927 5.09l.982.982 1.473-1.473-.982-.982zM10 5.139a4.872 4.872 0 00-4.862 4.86A4.872 4.872 0 0010 14.862 4.872 4.872 0 0014.86 10 4.872 4.872 0 0010 5.139zm0 1.389A3.462 3.462 0 0113.471 10a3.462 3.462 0 01-3.473 3.472A3.462 3.462 0 016.527 10 3.462 3.462 0 0110 6.528zM1.665 9.305v1.39h2.083v-1.39H1.666zm14.583 0v1.39h2.084v-1.39h-2.084zM5.09 13.928L3.616 15.4l.982.982 1.473-1.473-.982-.982zm9.82 0l-.982.982 1.473 1.473.982-.982-1.473-1.473zM9.305 16.25v2.083h1.389V16.25h-1.39z"/></svg>')`,
      },
    },
    '& .MuiSwitch-track': {
      opacity: 1,
      backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
      borderRadius: 20 / 2,
    },
  }));

  const blue = {
    50: '#F0F7FF',
    100: '#DAECFF',
    200: '#99CCF3',
    300: '#66B2FF',
    400: '#3399FF',
    500: '#007FFF',
    600: '#0072E5',
    900: '#003A75',
  };
  
  const grey = {
    50: '#f6f8fa',
    100: '#eaeef2',
    200: '#d0d7de',
    300: '#afb8c1',
    400: '#8c959f',
    500: '#6e7781',
    600: '#57606a',
    700: '#424a53',
    800: '#32383f',
    900: '#24292f',
  };

  const StyledListbox = styled('ul')(
    ({ theme }) => `
    box-sizing: border-box;
    padding: 0;
    margin: 10px 0;
    min-width: 150px;
    border-radius: 10px;
    overflow: auto;
    outline: 0px;
    background: ${theme.palette.mode === 'dark' ? grey[900] : '#fff'};
    border: 1px solid ${theme.palette.mode === 'dark' ? grey[700] : grey[200]};
    color: ${theme.palette.mode === 'dark' ? grey[300] : grey[900]};
    box-shadow: 0px 2px 16px ${theme.palette.mode === 'dark' ? grey[900] : grey[200]};
    z-index: 2;
    text-align: center;
    height: 123px;
    position: absolute;
    right: -28px;
    `,
  );
  
  const StyledMenuItem = styled(MenuItem)(
    ({ theme }) => `
    list-style: none;
    padding: 10px;
    border-radius: 8px;
    cursor: default;
    user-select: none;
    
  
    &:last-of-type {
      border-bottom: none;
    }
  
    &.${menuItemClasses.focusVisible} {
      outline: 3px solid ${theme.palette.mode === 'dark' ? blue[600] : blue[200]};
      background-color: ${theme.palette.mode === 'dark' ? grey[800] : grey[100]};
      color: ${theme.palette.mode === 'dark' ? grey[300] : grey[900]};
    }
  
    &.${menuItemClasses.disabled} {
      color: ${theme.palette.mode === 'dark' ? grey[700] : grey[400]};
    }
  
    &:hover:not(.${menuItemClasses.disabled}) {
      background-color: ${theme.palette.mode === 'dark' ? grey[800] : grey[100]};
      color: ${theme.palette.mode === 'dark' ? grey[300] : grey[900]};
    }
    `,
  );

  const IconButton = styled(MenuButton)(
    ({ theme }) => `
    line-height: 1.5;
    background: transparent;
    border: none;
    color: ${theme.palette.mode === 'dark' ? blue[300] : blue[500]};
    cursor: pointer;
    display: none;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 120ms;
  
    // &:hover {
    //   background: ${theme.palette.mode === 'dark' ? grey[800] : grey[50]};
    //   border-color: ${theme.palette.mode === 'dark' ? grey[600] : grey[300]};
    // }
  
    &:focus-visible {
      border-color: ${blue[400]};
      outline: 3px solid ${theme.palette.mode === 'dark' ? blue[500] : blue[200]};
    }
    @media screen and (max-width: 376px) {
      display: block;
    }
    `,
  );

  return (
    <div className="reverse">
      <nav className="nav-desktop">
        <ul className="nav-desktop__list">
          <li>
            <NavLink to="/Home" className={`nav-desktop__list__link nav-desktop__list__link--${theme.theme}`}>
              Home
            </NavLink>
          </li>
          <li>
            <NavLink to="/WallPapers" className={`nav-desktop__list__link nav-desktop__list__link--${theme.theme}`}>
              More WallPapers
            </NavLink>
          </li>
          <li>
            <NavLink to="/about" className={`nav-desktop__list__link nav-desktop__list__link--${theme.theme}`}>
              About
            </NavLink>
            </li>
        </ul>
      </nav>
      <Dropdown>
        <IconButton>
          <MoreVertIcon/>
        </IconButton>
        <Menu className={`nav `} slots={{ listbox: StyledListbox }}>         
          <StyledMenuItem onClick={createHandleMenuClick('Home')} className="nav__list__item">
            <NavLink to="/" className={`nav__list__link nav__list__link--${theme.theme}`}>
              Home
            </NavLink>
          </StyledMenuItem>
          <StyledMenuItem className="nav__list__item">
            <NavLink to="/WallPapers" className={`nav__list__link nav__list__link--${theme.theme}`}>
              WallPapers
            </NavLink>
          </StyledMenuItem>
          <StyledMenuItem className="nav__list__item">
            <NavLink to="/about" className={`nav__list__link nav__list__link--${theme.theme}`}>
              About
            </NavLink>
          </StyledMenuItem>      
        </Menu>
      </Dropdown>
      <FormGroup>
        <FormControlLabel
          control={<MaterialUISwitch onChange={handleChange} checked={checked} sx={{ m: 1 }} defaultChecked />}
        />
      </FormGroup>
    </div>
  );
};

export default Navigation;
